import * as React from 'react';
import {
  Table, Button, Drawer, Input, message,
  Select, Checkbox, Modal, Radio, Form,
  Upload, Icon
} from 'antd';
// import moment from 'moment';
import './index.less';
import axios from '../../../util/Axios';
import { RoleChildrenTable } from './roleChildrenTable';
import moment from 'moment'
import { sendToQiniu, concatImageString } from '../../../util/sendToQiniu';
import { CityCodeBox } from '../../../components/cityCodeBox';
const Dragger = Upload.Dragger;
const { Option } = Select;

const PopImage = (imgSrc: string) => (
  <img src={imgSrc} alt="" style={{ width: "100%" }} />
)

interface IAdminUsersPageProps {
  title?: string,
  form: {
    getFieldDecorator: any
    getFieldsError: any
    validateFieldsAndScroll: any
  }
}

function hasErrors(fieldsError: any) {
  return Object.keys(fieldsError).some(field => fieldsError[field]);
}

class AdminUsersPageWrap extends React.Component<IAdminUsersPageProps, any> {

  public state = {
    files: [],
    updateId: 1, // edit id id
    selectedRowKeys: [], // Check here to configure the default column
    selectedPetKeys: [],
    selectedPetTypeKeys: [],
    loading: false,
    filteredInfo: null,
    sortedInfo: "",
    visible: false,
    editVisible: false,
    addVisible: false,
    value: 2,  // 
    imageSrc: "", // 
    imageUrl: "",
    defaultRoleName: "", // 
    defalutSizeValue: 6, // 
    editSizeVisible: false,  // 
    tableData: [],
    resTableData: [],
    resRoleTableData: [],
    showImage: false,
    roleChildrenTableData: [],
    roleGroupId: 1,
    submitMenu: [],
    menus: [],
    menuName: "",
    menuUrl: "",
    menuAddMenuChildren: [],
    menusList: [],
    editGroupModal: false,
    editGroupData: [],
    groupData: [],
    radioValue: "",
    adminId: 1,
    name: "",
    pwd: "",
    realName: "",
    groupId: 1,
    phone: "",
    email: "",
    cityCode: "321000"
  };

  public componentDidMount() {
    const That = this
    axios({
      method: "get",
      url: "/adminusers/list"
    }).then((res: any) => {
      if (res.length > 0) {
        const tableData = res.map((item: any, index: number) => {
          return {
            key: item.id,  // key data id 
            name: item.name,
            nickname: item.nickname,
            image: item.icon ? item.icon : "",
            usertype: item.groupId ? item.groupId.roleName : "----",
            phone: item.tel,
            ip: item.ip ? item.ip : "----",
            icon: item.icon,
            realName: item.realName,
            cityCode: item.cityCode,
            registertime: item.ct ? moment(parseInt(item.ct, 10)).format('YYYY-MM-DD H:mm:ss') : "----",
            lastlogintime: item.lastLoginTime ? moment(parseInt(item.lastLoginTime, 10)).format('YYYY-MM-DD H:mm:ss') : "----",
          }
        })
        That.setState({
          "tableData": tableData,
          resTableData: res,
          resRoleTableData: res
        })
      }
    })
  }

  public onSelectChange = (selectedRowKeys: any) => {
    this.setState({ selectedRowKeys });
  }

  public handleChange = (pagination: any, filters: any, sorter: any) => {
    // console.log('Various parameters', pagination, filters, sorter);

    this.setState({
      filteredInfo: filters,
      sortedInfo: sorter,
    });
  }

  public handleDelete = (key: any) => {
    // console.log(key)
  }

  public addRole = () => {
    this.showDrawer()
  }

  public addAdminUser = () => {
    const { name, pwd, groupId } = this.state
    axios({
      method: "post",
      url: `/adminusers/add`,
      data: {
        name,
        pwd,
        groupId
      }
    }).then((res: any) => {
      if (res) {
        message.success("")
        setTimeout(() => {
          window.location.reload()
        }, 500);
      } else {
        message.error("")
      }
    })
  }

  public addMenu = (record: any) => {
    const That = this
    axios({
      method: "get",
      url: `/menu/list?roleGroupId=${record.key}`
    }).then((res: any) => {
      if (res) {
        That.setState({
          addVisible: true,
          menus: res,
          roleGroupId: record.key,
        })
      }
    })
  }

  public editRole = (record: any) => {
    const That = this
    // console.log(record.size === "----", record)
    axios({
      method: "get",
      url: `/role/menus?id=${record.key}`,
    }).then((res: any) => {
      if (res) {
        const roleChildrenTableData = res.map((item: any, index: number) => {
          return {
            key: item.id,
            name: item.menuName,
            details: {
              options: item.menuChildren,
              allOptions: item.allMenuChildren
            },
            menuChildrenIds: item.menuIds
          }
        })
        That.setState({
          editVisible: true,
          defaultRoleName: record.name,
          roleGroupId: record.key,
          "roleChildrenTableData": roleChildrenTableData
        })
      }
    })


  }

  public showDrawer = () => {
    const That = this
    axios({
      method: "get",
      url: `/role/list`
    }).then((res) => {
      if (res) {
        That.setState({
          groupData: res,
          visible: true,
        })
      }
    })
  };

  public deleteRole = () => {

    const selectedRowKeys = this.state.selectedRowKeys

    axios({
      method: "post",
      url: "/user/delete",
      data: {
        ids: selectedRowKeys
      }
    }).then((res: any) => {
      if (res) {
        message.success("")
        setTimeout(() => {
          window.location.reload()
        }, 500);
      } else {
        message.error("")
      }
    })

  }


  public editSubmitRole = (id: number, data: number[]) => {

    const { submitMenu }: { submitMenu: any[] } = this.state

    const targetMenu = submitMenu.map((item) => {
      if (item.id === id) {
        return {
          id,
          ids: data
        }
      } else {
        return item
      }
    })

    const target = submitMenu.find((item) => {
      return item.id === id
    })

    if (target) {
      this.setState({
        submitMenu: targetMenu
      })
    } else {
      targetMenu.push({
        "id": id,
        ids: data,
      })
      this.setState({
        submitMenu: targetMenu
      })
    }



  }

  public submitRole = () => {
    axios({
      method: "post",
      url: "/role/edit",
      data: {
        editArr: this.state.submitMenu
      }
    }).then((res: any) => {
      if (res) {
        message.success("")
        setTimeout(() => {
          window.location.reload()
        }, 500);
      } else {
        message.error("")
      }
    })
  }

  public onMenuSelectChange = (value: string, option: any) => {
    axios({
      method: "get",
      url: `/menu/path?name=${value}`,
    }).then((res: any) => {
      if (res) {
        // console.log(res)
        this.setState({
          menusList: res,
          menuUrl: value,
          menuName: option.props.children
        })
      }
    })
  }


  public onCheckboxSelectChange = (value: any[]) => {
    this.setState({
      menuAddMenuChildren: value
    })
  }

  public sumitAddMenu = () => {
    const { menuName, roleGroupId, menuUrl, menuAddMenuChildren } = this.state
    axios({
      method: "post",
      url: "/menu/add",
      data: {
        "roleGroupId": roleGroupId,
        "menuName": menuName,
        "menuUrl": menuUrl,
        "menuAddMenuChildren": menuAddMenuChildren
      }
    }).then((res: any) => {
      if (res) {
        message.success("")
        setTimeout(() => {
          window.location.reload()
        }, 1000);
      } else {
        message.error("")
      }
    })
  }

  public editGroup = (record: any) => {
    // console.log(record)
    const That = this
    axios({
      method: "get",
      url: `/role/list`
    }).then((res) => {
      if (res) {
        That.setState({
          editGroupModal: true,
          editGroupData: res,
          adminId: record.key
        })
      }
    })
  }

  public handleOk = () => {
    const { radioValue, adminId } = this.state
    axios({
      method: "post",
      url: `/adminusers/group`,
      data: {
        groupId: radioValue,
        "adminId": adminId
      }
    }).then((res) => {
      if (res) {
        message.success("")
        setTimeout(() => {
          window.location.reload()
        }, 500);
      } else {
        message.error("")
      }
    })
  }

  public handleCancel = () => {
    this.setState({
      editGroupModal: false
    })
  }

  public radioChange = (e: any) => {
    this.setState({
      radioValue: e.target.value
    })
  }

  public addRadioChange = (e: any) => {
    this.setState({
      groupId: e.target.value
    })
  }

  public handleSubmit = (e: any) => {
    e.preventDefault();
    const AvatarUrl = this.state.imageUrl
    this.props.form.validateFieldsAndScroll((err: any, values: {
      name: string,
      pwd: string, groupId: number, realName: string, email: string, phone: string, cityCode: string
    }) => {
      const { name, pwd, groupId, realName, email, phone, cityCode } = values
      if (!err) {
        // console.log('Received values of form: ', values);
        axios({
          method: "post",
          url: `/adminusers/add`,
          data: {
            name,
            pwd,
            realName,
            groupId,
            email,
            phone,
            cityCode,
            icon: AvatarUrl
          }
        }).then((res: any) => {
          if (res) {
            message.success("")
            setTimeout(() => {
              window.location.reload()
            }, 500);
          } else {
            message.error("")
          }
        })
      }
    });
  };

  public handleUpload = ({ fileList, file }: { fileList: any, file: any }) => {


    const imgName = file.name


    let adminId: string | null = localStorage.getItem("adminId")

    const timeStamp = new Date().getTime().toString()

    if (adminId) {
      while (adminId.length < 36) {
        adminId += "x"
      }
    } else {
      alert("")
    }

    const fileName = `${adminId}${timeStamp}ao12`

    fileList.map((item: any, index: number) => {
      if (item.name === imgName) {
        item.name = fileName
      }
      return
    })

    const upToken = localStorage.getItem('uploadToken')  // token

    if (upToken) {
      sendToQiniu(file, fileName, upToken, "ao") // 
    }

    const ImageString = concatImageString(fileList)

    this.setState({
      files: fileList,
      "imageUrl": ImageString
    })

  }

  public render() {

    const That = this

    const { selectedRowKeys, tableData, roleChildrenTableData, roleGroupId, menus, menusList } = this.state;

    let { filteredInfo }: any = this.state;

    filteredInfo = filteredInfo || {};

    const columns = [{
      title: '',
      dataIndex: 'name',
      key: 'name',
    }, {
      title: '',
      dataIndex: 'image',
      key: 'image',
      render: (text: any, record: any) => {

        if (record.image !== "") {

          const imageUrlList = record.image.split(',')

          const images = imageUrlList.map((item: string, index: number) => (
            <a href="javascript:void(0);" key={index} onClick={() => {
              That.setState({
                imageSrc: item,
                showImage: true,
              })
            }}></a>
          ))

          return images

        } else {

          return <span></span>

        }

      }
    }, {
      title: '',
      dataIndex: 'usertype',
      key: 'usertype',
    }, {
      title: '',
      dataIndex: 'realName',
      key: 'realName',
    },
    {
      title: '',
      dataIndex: 'cityCode',
      key: 'cityCode',
      render: (text: any, record: any) => {
        const type = record.cityCode === "321000" ? 0 : 1;
        if (record.cityCode) {
          return <CityCodeBox type={type} />
        } else {
          return "----"
        }
      }
    }, {
      title: '',
      dataIndex: 'phone',
      key: 'phone',
    },
    {
      title: 'IP',
      dataIndex: 'ip',
      key: 'ip',
    },
    {
      title: '',
      dataIndex: 'registertime',
      key: 'registertime',
    },
    {
      title: '',
      dataIndex: 'lastlogintime',
      key: 'lastlogintime',
    }, {
      title: '',
      dataIndex: 'operation',
      key: 'operation',
      render: (text: any, record: any) => {
        return (
          <span>
            <a href="javascript:;" onClick={this.editGroup.bind(this, record)} style={{ marginRight: 20 }}></a>
          </span>
        )
      }
    }];

    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange
    };

    const hasSelected = selectedRowKeys.length > 0;

    const selectOptions = menus.map((item: any, index: number) => {
      return (
        <Option value={item.path} key={index}>{item.name}</Option>
      )
    })

    const checkboxOptions = menusList.map((item: any, index) => {
      return {
        label: item.authName,
        value: item.id,
      }
    })

    const radios = this.state.editGroupData.map((item: any, index) => {
      return (
        <Radio.Button value={item.id} key={index}>{item.roleName}</Radio.Button>
      )
    })

    const addRadios = this.state.groupData.map((item: any, index) => {
      return (
        <Radio.Button value={item.id} key={index}>{item.roleName}</Radio.Button>
      )
    })

    return (
      <div>
        <div style={{ marginBottom: 16, textAlign: "left" }}>
          <Button type="primary" onClick={this.addRole.bind(this)} style={{ marginRight: 20 }}>
            
          </Button>
          <Button type="danger" disabled={!hasSelected} onClick={() => {
            this.deleteRole()
          }}>
            
          </Button>
          <span style={{ marginLeft: 8 }}>
            {hasSelected ? `${selectedRowKeys.length}` : ''}
          </span>
        </div>
        <div>
          <Table rowSelection={rowSelection} columns={columns} dataSource={tableData}
            pagination={{ pageSize: 10 }}
            onChange={this.handleChange}
            locale={{ filterConfirm: "", filterReset: "", emptyText: "" }}
          />
        </div>
        <div>
          <Drawer
            width={500}
            title=""
            placement="right"
            closable={true}
            onClose={() => { this.setState({ visible: false, files: [] }) }}
            visible={this.state.visible}
          >
            <Form onSubmit={this.handleSubmit}>
              <Form.Item label="" hasFeedback={true} style={{ marginBottom: 0 }}>
                {this.props.form.getFieldDecorator('name', {
                  initialValue: "",
                  setFieldsValue: this.state.name,
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input placeholder="" onChange={(e: any) => { this.setState({ name: e.target.value }) }} />
                )}
              </Form.Item>
              <Form.Item label="" hasFeedback={true} style={{ marginBottom: 0 }}>
                {this.props.form.getFieldDecorator('pwd', {
                  initialValue: "",
                  setFieldsValue: this.state.pwd,
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input placeholder="" onChange={(e: any) => { this.setState({ name: e.target.value }) }} />
                )}
              </Form.Item>
              <Form.Item label="" hasFeedback={true} style={{ marginBottom: 0 }}>
                {this.props.form.getFieldDecorator('realName', {
                  initialValue: "",
                  setFieldsValue: this.state.realName,
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input placeholder="" onChange={(e: any) => { this.setState({ realName: e.target.value }) }} />
                )}
              </Form.Item>
              <Form.Item label="" hasFeedback={true} style={{ marginBottom: 0 }}>
                {this.props.form.getFieldDecorator('phone', {
                  initialValue: "",
                  setFieldsValue: this.state.phone,
                  rules: [{ required: true, message: '!', pattern: /^1[0-9]{10}$/ }],
                })(
                  <Input placeholder="" onChange={(e: any) => { this.setState({ phone: e.target.value }) }} />
                )}
              </Form.Item>
              <Form.Item label="" hasFeedback={true} style={{ marginBottom: 0 }}>
                {this.props.form.getFieldDecorator('email', {
                  initialValue: "",
                  setFieldsValue: this.state.email,
                  rules: [{
                    required: false,
                    message: '!'
                  }],
                })(
                  <Input placeholder="" onChange={(e: any) => { this.setState({ email: e.target.value }) }} />
                )}
              </Form.Item>
              <Form.Item label="" style={{ marginBottom: 0 }}>
                {this.props.form.getFieldDecorator('groupId', {
                  initialValue: 1,
                  setFieldsValue: this.state.groupId,
                  rules: [{ required: true, message: '!' }],
                })(
                  <Radio.Group buttonStyle="solid"
                    onChange={this.addRadioChange.bind(this)}
                  >
                    {
                      addRadios
                    }
                  </Radio.Group>)}
              </Form.Item>
              <Form.Item label="" style={{ marginBottom: 0 }}>
                {this.props.form.getFieldDecorator('cityCode', {
                  initialValue: this.state.cityCode,
                  setFieldsValue: this.state.cityCode,
                  rules: [{ required: true, message: '!' }],
                })(
                  <Radio.Group buttonStyle="solid"
                    onChange={(e: any) => { this.setState({ cityCode: e.target.value }) }}
                  >
                    <Radio.Button value="321000"></Radio.Button>
                    <Radio.Button value="340300"></Radio.Button>
                  </Radio.Group>)}
              </Form.Item>
              <Form.Item label="" style={{ marginBottom: 0 }}>
                <Dragger className="Dragger" onChange={this.handleUpload} beforeUpload={(file: any) => {
                  this.setState({
                    files: [...this.state.files, file]
                  })
                  return false
                }}>
                  <p className="ant-upload-drag-icon">
                    <Icon type="inbox" />
                  </p>
                  <p className="ant-upload-hint"></p>
                </Dragger>
              </Form.Item>
              <div
                style={{
                  position: 'absolute',
                  left: 0,
                  bottom: 0,
                  width: '100%',
                  borderTop: '1px solid #e9e9e9',
                  padding: '10px 16px',
                  background: '#fff',
                  textAlign: 'right',
                }}
              >
                <Button onClick={() => { this.setState({ visible: false }) }} style={{ marginRight: 8 }}>
                  
                </Button>
                <Button type="primary" block={false} htmlType="submit"
                  className="login-form-button"
                  disabled={hasErrors(this.props.form.getFieldsError())}>
                  
                </Button>
              </div>
            </Form>
          </Drawer>
        </div>
        <div>
          <Drawer
            width={800}
            title=""
            placement="right"
            closable={true}
            onClose={() => {
              this.setState({ editVisible: false, files: [] })
            }}
            visible={this.state.editVisible}
          >
            <div style={{ marginBottom: 25 }}>
              <span></span>
              <Input style={{ width: "70%" }} placeholder="" onChange={(e: any) => { this.setState({ defaultRoleName: e.target.value }) }}
                defaultValue={this.state.defaultRoleName}
                value={this.state.defaultRoleName}
              />
            </div>
            <RoleChildrenTable tableData={roleChildrenTableData} roleGroupId={roleGroupId} editData={this.editSubmitRole.bind(this)} />
            <div
              style={{
                position: 'absolute',
                left: 0,
                bottom: 0,
                width: '100%',
                borderTop: '1px solid #e9e9e9',
                padding: '10px 16px',
                background: '#fff',
                textAlign: 'right',
              }}
            >
              <Button onClick={() => { this.setState({ editVisible: false }) }} style={{ marginRight: 8 }}>
                
              </Button>
              <Button onClick={() => {
                this.submitRole()
              }}
                type="primary">
                
            </Button>
            </div>
          </Drawer>
          <Drawer
            width={500}
            title=""
            placement="right"
            closable={true}
            onClose={() => {
              this.setState({ addVisible: false, })
            }}
            visible={this.state.addVisible}
          >
            <div style={{ marginBottom: 25 }}>
              <span></span>
              <Select style={{ width: 320 }} onChange={this.onMenuSelectChange.bind(this)}>
                {selectOptions}
              </Select>
            </div>
            <div style={{ marginTop: 25 }}>
              <span style={{ marginRight: 8 }}>: </span>
              {
                checkboxOptions.length > 0 ?
                  <Checkbox.Group options={checkboxOptions} onChange={this.onCheckboxSelectChange.bind(this)} style={{
                    display: "block",
                    marginTop: 16
                  }} />
                  :
                  <span></span>
              }
            </div>
            <div
              style={{
                position: 'absolute',
                left: 0,
                bottom: 0,
                width: '100%',
                borderTop: '1px solid #e9e9e9',
                padding: '10px 16px',
                background: '#fff',
                textAlign: 'right',
              }}
            >
              <Button onClick={() => { this.setState({ addVisible: false }) }} style={{ marginRight: 8 }}>
                
              </Button>
              <Button onClick={this.sumitAddMenu.bind(this)}
                type="primary">
                
            </Button>
            </div>
          </Drawer>
          <Modal
            title=""
            visible={this.state.editGroupModal}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            okText=""
            cancelText=""
          >
            <div>
              <Radio.Group defaultValue="a" buttonStyle="solid"
                onChange={this.radioChange.bind(this)}
              >
                {
                  radios
                }
              </Radio.Group>
            </div>
          </Modal>
          <Modal
            visible={this.state.showImage}
            onCancel={() => this.setState({ showImage: false })}
            footer={null}
            destroyOnClose={true}
          >
            {PopImage(this.state.imageSrc)}
          </Modal>
        </div>
      </div >
    )
  }
}

const AdminUsersPage = Form.create({ name: 'validate' })(AdminUsersPageWrap);

export default AdminUsersPage