import * as React from 'react';
import { Tag } from "antd";

interface ICityCodeBoxProps {
    type: number
}

export class CityCodeBox extends React.Component<ICityCodeBoxProps> {

    public render() {
        const Boxes = [{
            color: "#607d8b",
            content: ""
        },{
            color: "#03a9f4",
            content: ""
        },]
        return (
            <div>
                <Tag color={Boxes[this.props.type].color}>{Boxes[this.props.type].content}</Tag>
            </div>
        );
    }
}
